require('./popups.scss')

const give_up_loan_task_popup = {
  options: {
    effect: 'scale',
    cssClass: 'give-up-loan-popup',
    showClose: false,
    buttons: [
      { text: '放弃' },
      {text: '取消'}
    ]
  },

  template: () => {
    return `
      <div class="title">您的贷款申请还差2步就完成啦确定要放弃吗？</div>
    `
  }
}

module.exports = {
  give_up_loan_task_popup
}



