<template>
  <div class="page page-loan-detail" v-nav="{hideNavbar: true}">
    <AnimateHeader
      bg-color="#1e5188"
      menu-bg-color="#1d4a7a"
      color="#ffffff"
      :expanded="true"
      :animate="false"
      :show-back-button="false"
      :show-menu-button="true"
      :on-menu-button-click="back"
      >
      <div slot="content">
        <LoanDetailTitle>
          <img v-if="loanDetail.icon" :src="loanDetail.icon" class="logo" alt="">
          <div class="title">{{ loanDetail.title }}</div>
          <div class="tags">
            <span v-for="(tag, index) in loanDetail.tags" :key="index" v-text="tag"></span>
          </div>
        </LoanDetailTitle>
      </div>
    </AnimateHeader>
    <div class="page-content">
      <scroller style="z-index: 0; padding-bottom: 44px !important;">
        <div class="loan-info">
          <div class="rang-title">额度范围</div>
          <div class="loan-rang">{{ loanDetail.loan_range }}<span class="unit">元</span></div>
          <span class="info-wrapper">
            <div class="title">{{ loanDetail.rate_type }}</div>
            <div class="content">{{ loanDetail.rate }}<span class="unit">%</span></div>
          </span>
          <span class="info-wrapper">
            <div class="title">最快放款</div>
            <div class="content">{{ loanDetail.loan_time }}<span class="unit">{{ loanDetail.loan_time_unit }}</span></div>
          </span>
          <span class="info-wrapper">
            <div class="title">期限范围</div>
            <div class="content">{{ loanDetail.loan_time_range }}<span class="unit">{{ loanDetail.loan_time_range_unit }}</span></div>
          </span>
          <span class="info-wrapper">
            <div class="title">还款方式</div>
            <div class="content small">{{ loanDetail.repayment_type }}<span class="unit"></span></div>
          </span>
        </div>
        <div class="loan-condition">
          <div class="title"><img src="//assets.qkcdn.com/images/8790e993b21929185bc714f49429f36b.png" alt="" class="tip-logo">申请条件</div>
          <div class="desc" v-for="(term, index) in loanDetail.apply_term">{{ term }}</div>
        </div>
        <div class="loan-condition">
          <div class="title"><img src="//assets.qkcdn.com/images/d11374765a87fd9ee6dc6188be0f7e0e.png" alt="" class="tip-logo">准备材料</div>
          <div class="desc" v-for="(metaria, index) in loanDetail.prepare_data">{{ metaria }}</div>
        </div>
        <div class="loan-condition">
          <div class="title"><img src="//assets.qkcdn.com/images/bb34070db14f77b33956d0809022b201.png" alt="" class="tip-logo">审核说明</div>
          <div class="desc" v-for="(explain, index) in loanDetail.verify_explain">{{ explain }}</div>
        </div>

        <div style="height: 44px;"></div>
      </scroller>

      <div class="btn-apply" :class="{'active': showBtnApply}">
        <div class="btn-txt" @click="apply()">立即申请</div>
      </div>
    </div>
  </div>
</template>

<style lang="scss" scoped>
  @import "~scss/mixins";

  .page-loan-detail {
    background-color: #f5f5f5;

    .page-content {
      margin-top: 100px;

      @include font-regular();

      .loan-info {
        background: #fff;
        margin-bottom: 10px;
        padding: 20px 0px;
        font-size: 0px;

        .rang-title {
          font-size: 12px;
          line-height: 17px;
          color: #b5b5b5;
          padding-left: 16px;
        }

        .loan-rang {
          font-size: 24px;
          font-weight: 600;
          padding-left: 16px;
          color: #fc7421;
          margin-bottom: 10px;
          line-height: 33px;

          .unit {
            font-size: 12px;
            line-height: 17px;
            font-weight: 600;
            color: #fc7421;
            display: inline-block;
            margin-left: 2px;
            position: relative;
            top: -1.5px;
          }
        }

        .info-wrapper:nth-child(3) {
          padding-left: 16px;
        }

        .info-wrapper {
          display: inline-block;
          padding: 0 15px;
          vertical-align: top !important;

          .title {
            font-size: 12px;
            color: #b5b5b5;
            line-height: 17px;
          }

          .content {
            font-size: 20px;
            font-weight: 600;
            line-height: 28px;
            color: #3a3a3a;

            &.small {
              font-size: 14px;
            }

            .unit {
              font-size: 11px;
              line-height: 16px;
              font-weight: normal;
              display: inline-block;
              margin-left: 2px;
              transform: translate3d(0, -4%, 0);
            }
          }
        }
      }

      .loan-condition {
        background: #fff;
        margin-bottom: 10px;
        padding: 20px 16px;

        &:last-child {
          margin-bottom: 28px;
        }

        .title {
          font-size: 12px;
          line-height: 17px;
          color: #3a3a3a;
          margin-bottom: 8px;

          .tip-logo {
            width: 12px;
            height: 12px;
            position: relative;
            top: 2px;
            margin-right: 4px;
          }
        }

        .desc {
          font-size: 14px;
          color: #3a3a3a;
          margin-bottom: 2px;

          &:last-child {
            margin-bottom: 0px;
          }
        }
      }

      .btn-apply {
        position: absolute;
        bottom: -44px;
        left: 0;
        width: 100%;
        height: 50px;
        background-color: #2b619b;
        z-index: 2;
        transition: bottom 0.15s;

        &.active {
          bottom: 0px;
        }

        .btn-txt {
          width: 100%;
          line-height: 50px;
          font-size: 13px;
          font-weight: 600;
          text-align: center;
          color: #ffffff;
        }
      }
    }
  }
</style>

<script>
  import AnimateHeader from '@/widget/animate-header/Index.vue'
  import LoanDetailTitle from '../components/LoanDetailTitle.vue'
  import { give_up_loan_task_popup } from '../popups'
  import {mapState, mapActions} from 'vuex'
  import {KEY_LOAN_APPLY_CLICK} from 'constants'

  export default {
    components: {
      AnimateHeader,
      LoanDetailTitle
    },

    data () {
      return {
        loan_apply_click_key: '', // 记录申请是否点击的key
        showBtnApply: false
      }
    },

    computed: {
      ...mapState('loan', [
        'loanDetail'
      ])
    },

    mounted () {
      this.loan_apply_click_key = `${KEY_LOAN_APPLY_CLICK}_${this.$route.params.id}`
      this.fetchDetail(this.$route.params.id)

      setTimeout(() => { this.showBtnApply = true }, 500)
    },

    methods: {
      ...mapActions('loan', [
        'fetchDetail'
      ]),

      back () {
        if ($storage.get(this.loan_apply_click_key)) {
          $storage.remove(this.loan_apply_click_key)
          $router.back('/loan')
        } else {
          let popup = $popup.fromTemplate(
            give_up_loan_task_popup.template(),
            give_up_loan_task_popup.options
          )
          popup.show().then(index => {
            if (index === 0) {
              $storage.remove(this.loan_apply_click_key)
              $router.back('/loan')
            }
          })
        }
      },

      apply () {
        $storage.set(this.loan_apply_click_key, true)
        location.href = this.loanDetail.url
      }
    }
  }
</script>



